        "Service_title1": "Konsultation mit einem Arzt fr Physikalische und Rehabilitative Medizin",
        "Service_text1": "Beurteilung des psycho-emotionalen Funktionszustandes des Patienten zu Beginn der Rehabilitation unter Bercksichtigung der Daten aus dem Gutachten der Wehrrztlichen Kommission, der Anamnese und der Lebensgeschichte. Erstellung eines individuellen Rehabilitationsplans, Empfehlungen zu Dit und Ernhrung in Abhngigkeit von den Untersuchungsergebnissen, der Konstitution des Patienten und den Besonderheiten der Biographie.",
        "Service_title2": "Psychologische Beratung",
        "Service_text2.1": "Jeder, der vom Krieg betroffen ist, sprt in unterschiedlichem Ausma seine zerstrerische Wirkung. Schlielich geht die Teilnahme an Feindseligkeiten immer mit einer Stressreaktion des Krpers einher, die ohne entsprechende rechtzeitige und qualifizierte Hilfe nachteilige Folgen sowohl fr den Menschen selbst und seine Angehrigen als auch fr die Gesellschaft als Ganzes haben kann. Die Arbeit eines Psychologen ist eine mhsame und komplexe Arbeit mit den unsichtbaren Folgen des Krieges. Inhalt der psychologischen Hilfe im weiteren Sinne ist die emotionale, semantische und existentielle Untersttzung einer Person in schwierigen Situationen, die im Laufe ihres persnlichen oder sozialen Lebens auftreten. Die Arbeit eines Psychologen in unserem Zentrum hat einen definierten Begriff und besteht aus drei Schritten:",
        "Service_text2.2": "Verfahrensdiagnostik des Zustands  um die Tiefe des Traumas und die geeigneten psychologischen Heilmittel in jedem Einzelfall zu ermitteln.",
        "Service_text2.3": "Um den Zustand zu stabilisieren, mssen Sie die Kontrolle ber Ihre emotionalen Reaktionen zurckgewinnen. Dabei handelt es sich in erster Linie um eine Arbeit mit den inneren Ressourcen eines Menschen. Das Erreichen eines Ressourcenzustands bedeutet, die krperliche, geistige und mentale Kraft und Energie zu erlangen, um normal zu funktionieren.",
        "Service_text2.4": "Persnlicher Reset  Festlegung eines individuellen Lebensplans  Einordnung traumatischer Ereignisse in die zeitliche Gesamtperspektive des eigenen Lebens und der persnlichen Geschichte.",
        "Service_title3": "Postisometrische Entspannung",
        "Service_text3": "Eine Technik zur Entspannung der Muskeln, auch in den tiefen Segmenten des Bewegungsapparats. Die postisometrische Entspannung basiert auf sanften manuellen Techniken zur Steigerung der Beweglichkeit der Wirbelsule und Gelenke und zur Wiederherstellung der Elastizitt von Muskeln und Bndern.",
        "Service_title4": "Reflexzonenmassage",
        "Service_text4": "Bei der Reflexzonenmassage handelt es sich um die Einwirkung auf aktive Punkte des menschlichen Krpers. eine der wenigen alternativen Therapien, die auch von Vertretern der offiziellen Medizin als wirksam und sicher anerkannt werden. Es kommt zu einer mehrstufigen Reflexreaktion, bei der sich der Krper selbst regeneriert. Diese Methode kann eine starke Wirkung auf den gesamten Krper haben, die Schutzreserven des Menschen aktivieren, den Hormonspiegel und Stoffwechselprozesse normalisieren, die Immunitt strken und eine beruhigende und entspannende Wirkung haben.",
        "Service_title5": "Manuelle Therapie der Wirbelsule und Gelenke",
        "Service_text5": "Die manuelle Therapie der Wirbelsule und Gelenke kombiniert Massage, Muskelentspannungstechniken, manuelle Traktionstechniken und Kinesiotherapie. Dabei wird der Zug auf bestimmte Bereiche der Wirbelsule durch die Hnde des Therapeuten schrittweise erhht. Die Wirbelsule wird gedehnt, um die Beweglichkeit zu verbessern und Schmerzen zu lindern. So werden Wirbelsulenerkrankungen wie Wirbelsulenhernien, Skoliose oder Bandscheibenvorflle behandelt. Durch Zug auf Muskeln und Gelenke wird die Beweglichkeit verbessert und Schmerzen gelindert. In der Wirbelsulen- und Gelenktherapie werden spezielle Techniken zum Einreiben von Salben und len aus natrlichen Substanzen angewendet, die beruhigend, schmerzstillend und wrmend wirken, die Durchblutung frdern, Schwellungen lindern und den Zustand des Knorpelgewebes verbessern. Methoden der Pharmakopunktur werden in der Therapie der Wirbelsule und der Gelenke eingesetzt.",
        "Service_title6": "Rhythmisches Reiben",
        "Service_text6.1": "Beim rhythmischen Einreiben handelt es sich um eine Arbeit mit dem Patienten, bei der sowohl Arzneimittel als auch speziell zubereitete Salben und le in der Therapie kombiniert eingesetzt werden. Wir wenden Methoden zur uerlichen Anwendung an, indem wir sie mit Bewegungen einer bestimmten Richtung in den Krper einreiben, die auf den anatomischen und physiologischen Besonderheiten der Bildung des menschlichen Krpers beruhen.",
        "Service_text6.2": "Die Therapie bercksichtigt sowohl die konstitutionellen Merkmale des Patienten, was bei der Auswahl der Wirkung auf das Organsystem entsprechend seiner Konstitution (Herztyp, Nierentyp, Lebertyp, Lungentyp) hilft, als auch den Ort der Symptome der erkannten Strungen. Darauf aufbauend werden geeignete Krperbereiche fr die Therapie ausgewhlt.",
        "Service_title7": "Kinesiotherapie",
