        "Service_title9": "ldispersionsbder",
        "Service_text9.1": "In Deutschland wird diese Therapieform seit 1937 angewandt und ist seit 1971 als JUNGEBAD-Therapie patentiert.",
        "Service_text9.2": "Die moderne Physiologie zeigt, dass Wrme fr den menschlichen Krper wichtiger ist als die Atmung. Thermische Prozesse stehen in direktem Zusammenhang mit der Blutzirkulation und die Funktion des Organs ist von der Blutversorgung abhngig. Die Krpertemperatur des Menschen ist vom Tag-Nacht-Rhythmus abhngig und ist nachts hher als tagsber. Dies ist wichtig fr eine erholsame Nachtruhe.",
        "Service_text9.3": "ldispersionsbder sind eine Technik, die auf der Verteilung speziell zubereiteter le durch eine ldispersionsvorrichtung in Wassertropfen basiert. Dadurch entsteht eine stabile Verbindung zwischen l und Wasser fr mehrere Stunden. Die Wassertemperatur im Bad betrgt 3738 C, was nahe an der physiologischen Temperatur des menschlichen Krpers liegt, und die thermische qualitative Wirkung des ls selbst beeinflusst aktiv die Blutzirkulationsprozesse im gesamten Krper.",
        "Service_text9.4": "Begleitet wird die Prozedur von einer Massage in einer gefllten Badewanne, die der Therapeut je nach Reaktionsempfindlichkeit der Haut des Patienten sowohl mit Spezialbrsten als auch manuell durchfhrt. Nach dem Bad selbst ist das Einwickeln des Patienten und eine Ruhepause von 3040 Minuten im Liegen ein notwendiger Bestandteil der therapeutischen Prozedur.",
        "Service_text9.5": "Die beschriebene Therapieform hat eine tiefgreifende Wirkung auf den Krper und direkt auf seine Temperaturkomponente, was sich nicht nur positiv auf die Therapie seines krperlichen Zustands auswirkt, sondern auch auf die Harmonisierung auf psycho-emotionaler Ebene. Die Ergebnisse der Therapie, die am hufigsten von den Patienten selbst bemerkt werden, sind verbesserter Schlaf und positive psycho-emotionale Vernderungen des Gesundheitszustands.",
